import { RenderNodeProps, ZhiFactory } from '@ali/4ever-cangjie';
import React from 'react';
import { InlineCodeComp } from '../components/styled';
import inlineCodeMoFactory from '../mo';

const zhiFactory: ZhiFactory = () => {
  // zhi中不涉及编辑态，但无论是否在灰度中，均需要支持解析&渲染新模型的能力
  return {
    ...inlineCodeMoFactory({}),
    renderNode: {
      inlineCode: (props: RenderNodeProps, controller) => {
        const { children, attributes } = props;
        return <InlineCodeComp {...attributes}>{children()}</InlineCodeComp>;
      },
    },
  };
};

export default zhiFactory;
